.gym {
  &__content {
    position: relative;
    padding-top: 118px;
    padding-bottom: 218px;
    background-image: url("../img/gym-desktop@1x.jpg");
    background-size: 660px 401px;
    background-repeat: no-repeat;
    background-position: 85.5% 117px;

    @media (min-resolution: 1.5dppx) {
      background-image: url("../img/gym-desktop@2x.jpg");
    }

    @include media-tablet {
      flex-direction: column;
      align-items: center;
      padding-top: 96px;
      padding-bottom: 179px;
      background-image: url("../img/gym-tablet@1x.jpg");
      background-size: 678px 345px;
      background-position: 50% calc(100% - 183px);

      @media (min-resolution: 1.5dppx) {
        background-image: url("../img/gym-tablet@2x.jpg");
      }
    }

    @include media-mobile {
      padding-left: 0;
      padding-right: 0;
      padding-top: 70px;
      padding-bottom: 66px;
      background-image: url("../img/gym-mobile@1x.jpg");
      background-size: 320px 313px;
      background-position: 50% calc(100% - 273px);

      @media (min-resolution: 1.5dppx) {
        background-image: url("../img/gym-mobile@2x.jpg");
      }
    }
  }

  &__text {
    display: flex;
    flex-direction: column;
    margin-right: auto;
    padding-right: 676px;
    padding-top: 47px;
    padding-bottom: 47px;
    padding-left: 5.1%;
    max-width: 1091px;
    min-width: 290px;

    @include media-tablet {
      align-items: center;
      margin-right: 0;
      padding-right: 0;
      padding-top: 0;
      padding-bottom: 349px;
      padding-left: 0;
      min-width: auto;
      max-width: 502px;
    }

    @include media-mobile {
      @include mobile-paddings;
      margin-left: -40px;
      padding-bottom: 353px;
      align-items: flex-start;
      max-width: 280px;
    }

    & h2 {
      @include h2-heading;
      margin-bottom: 8px;
      min-width: 290px;

      @include media-tablet {
        min-width: auto;
        width: 290px;
      }

      @include media-mobile {
        margin-bottom: 6px;
        width: 220px;
      }
    }

    & p {
      @include text-block-reset;
      min-width: 290px;
      color: $main-dark-blue;
      font-family: $roboto;
      font-weight: 300;
      font-size: 16px;
      line-height: 24px;

      &:first-of-type {
        margin-bottom: 39px;
        font-size: 18px;
        font-weight: bold;
        line-height: 27px;

        @include media-tablet {
          margin-bottom: 35px;
          width: 290px;
        }

        @include media-mobile {
          margin-bottom: 23px;
          width: 279px;
          font-size: 18px;
          font-weight: 400;
          line-height: 27px;
        }
      }

      &:not(:last-of-type):not(:first-of-type) {
        margin-bottom: 8px;
      }

      @include media-tablet {
        min-width: auto;
        width: 502px;
        text-align: center;

        &:not(:last-of-type):not(:first-of-type) {
          margin-bottom: 43px;
        }

        &:last-of-type {
          display: none;
        }
      }

      @include media-mobile {
        &:not(:last-child):not(:first-of-type) {
          margin-bottom: 33px;
        }
        width: 258px;
        text-align: left;
      }
    }
  }

  &__video {
    flex-shrink: 0;
    position: absolute;
    top: 405px;
    left: calc(50% + 40px);
    transform: translateX(-50%);
    width: 364px;
    height: 228px;
    background-image: url("../img/gym-video-desktop@1x.jpg");
    cursor: pointer;

    &::before {
      content: "";
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translateX(-50%) translateY(-50%);
      width: 100px;
      height: 100px;
      background-image: url("../img/svg/play.svg");
      background-size: cover;

      transition-duration: 0.2s;
    }

    &:hover::before {
      width: 110px;
      height: 110px;
    }

    &:active::before {
      opacity: 0.7;
    }


    @media (min-resolution: 1.5dppx) {
      background-size: cover;
      background-image: url("../img/gym-video-desktop@2x.jpg");
    }

    @media (max-width: 1287px) {
      left: calc(50% + 210px);
    }

    @include media-tablet {
      top: auto;
      bottom: 100px;
      left: 50%;
      width: 266px;
      height: 167px;
      background-image: url("../img/gym-video-tablet@1x.jpg");

      @media (min-resolution: 1.5dppx) {
        background-image: url("../img/gym-video-tablet@2x.jpg");
      }
    }

    @include media-mobile {
      position: static;
      transform: translateX(0);

      width: 320px;
      height: 167px;
      background-image: url("../img/gym-video-mobile@1x.jpg");

      @media (min-resolution: 1.5dppx) {
        background-image: url("../img/gym-video-mobile@2x.jpg");
      }
    }
  }
}
